.messages{
    display: grid;
    grid-template-columns: 9fr 3fr;
    height: 94vh;
}

.dialog{
    background-color: #ffffff;
    border-right: 1px solid #EBEBEB;
    height: 100vh;
}
.dialogItems{
    background-color: #ffffff;
    
}
.dialogHead{
    border-bottom: 1px solid #EBEBEB;
    min-height: 80px;
    display: flex;
    align-items: center;

}
.dialogBody{
    padding: 50px;
   
}




.messageOut{
    margin-left: auto;
    background-color: #E3EFFF;
}


.messagesFill{
    
    padding: 20px;
    
}
.formContainer{
    width: 100%;
    background: #fafbfc;
}
.avatar{
    height: 50px;
    width: 50px;
    border-radius: 50%;
    
}
.avatarContainer{
    margin-left: 20px;
}
.name{
    margin-left: 20px;
    font-size: 20px;
    font-weight: 600;
}
.dialogHead a{
    text-decoration: none;
    color: #454545;
    
}
.formContainer{
    position: relative;
}
.formContainer textarea{
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    display: block;
    border: none;
    border-top: 1px solid #EBEBEB;
    padding: 20px 40px;
    padding-right: 80px;
    font-size: 24px;
    font-weight: 500px;
    font-family: inherit;
}

.buttonForm{
    
    /*Reset's every elements apperance*/
    background: none repeat scroll 0 0 transparent;
    border: medium none;
    border-spacing: 0;
    font-family: 'PT Sans Narrow',sans-serif;
    font-weight: normal;
    list-style: none outside none;
    margin: 0;
    padding: 0;
    text-align: left;
    text-decoration: none;
    text-indent: 0;
    display: block;
    cursor: pointer;
     top: 25px;
    right:  25px;
    content: '';
    position: absolute;
    width: 40px;
    height: 40px;
    background: url(../../../img/send-arrow.png) no-repeat;
    background-size: cover;
    outline: none;
}
.emptyMessages{
    background-color: #ffffff;
    height: 100vh;
    border-right: 1px solid #EBEBEB;
    display: flex;
    
    flex-direction: column;
}

.selectDialog{
    margin: 0 auto;
    color: #929eb0;
    font-size: 20px;
    position: relative;
}
.iconMessage{
    background: url(../../../img/empty_icon.png) no-repeat ;
    background-size: cover;
    width: 135px;
    height: 130px;
   
    margin: 0 auto;
    margin-top: 300px;
}
.dialogSearch{
    border-bottom: 1px solid #EBEBEB;
    min-height: 80px;
    position: relative;
    display: flex;
    justify-content: center;
    flex-direction: column;
}
.dialogSearch::after{
    content: '';
    position: absolute;
    background: url(../../../img/search.png);
    width: 32px;
    height: 32px;
    left: 10px;
}
.searchInput{
    display: block;
    padding-left: 50px;
    width: 100%;
    outline: none;
    border: none;
    font-size: 16px;
    
}
.buttomEmoji{
    position: absolute;
    top: 25px;
    right:  80px;
    cursor: pointer;
    font-size: 25px;
    width: 30px;
    height: 30px;
    background: url(../../../img/icon-smile-picker.png) no-repeat;
}
.emojyPickerP{
    position: absolute;
}